// Here's where the magic happens
// import { values as val } from './constants';
const val = require('./constants');


const equations = {
    testEQ: 'Are we there yet?',
    PressureAltitudeTakeoff: (fieldElevationTakeoff, altimeterTakeoff) => {
        const PA = ((fieldElevationTakeoff) + ((val.C1 * (1 - (Math.pow((altimeterTakeoff / val.C2), val.C3))))));
        return PA;
    },
    PressureAltitudeLanding: (fieldElevationLanding, altimeterLanding) => {
        const PA = ((fieldElevationLanding) + ((val.C1 * (1 - (Math.pow((altimeterLanding / val.C2), val.C3))))));
        return PA;
    },
    DensityAltitudeTakeoff: (pressAltTakeoff, tempCTakeoff) => {
        const DA = (pressAltTakeoff) + 
            ((val.C4 - val.C5*pressAltTakeoff) / val.C5) * 
            (1 - Math.pow(((val.C4-val.C5*pressAltTakeoff) / (tempCTakeoff+val.C6)), val.C7));
        return DA;
    },
    DensityAltitudeLanding: (pressAltLanding, tempCLanding) => {
        const DA = (pressAltLanding) + 
            ((val.C4 - val.C5*pressAltLanding) / val.C5) * 
            (1 - Math.pow(((val.C4-val.C5*pressAltLanding) / (tempCLanding+val.C6)), val.C7));
        return DA;
    },
    TakeoffRoll: (weightTakeoff, tempCTakeoff, pressAltTakeoff, headwindTakeoff) => {
        let x;

        if (headwindTakeoff >= 0) { x = 9 } else { x = 2 }

        const TORoll = ((val.C8) + val.C9 *  weightTakeoff + val.C10 *  tempCTakeoff + val.C11 * pressAltTakeoff +
            ( weightTakeoff - val.C12) * (( weightTakeoff - val.C12) * val.C13) + ( tempCTakeoff - val.C14) *
            (( weightTakeoff - val.C12) * val.C15) + ( tempCTakeoff - val.C14) * (( tempCTakeoff - val.C14) * val.C16) + 
            ( pressAltTakeoff - val.C17) * (( weightTakeoff - val.C12) * val.C18) + ( pressAltTakeoff - val.C17) * 
            (( tempCTakeoff - val.C14) * val.C19) + ( pressAltTakeoff - val.C17) * (( pressAltTakeoff - val.C17) * 
            val.C20)) * (1 -headwindTakeoff*0.1/x);

        return TORoll;
    },
    LiftoffSpeed: (weightTakeoff) => {
        const LOSpeed = (val.C21) + val.C22 *  weightTakeoff;
        return LOSpeed;
    },
    LiftoffSpeed50ftBarrier: (weightTakeoff) => {
        const LOSpeed50ft = val.C23+val.C24*weightTakeoff-val.C25*Math.pow((weightTakeoff-val.C26),2);
        return LOSpeed50ft;
    },
    AccelerateStop: (takeoffRoll) => {
        const accelStop = takeoffRoll * 2.5;
        return accelStop;
    },
    TakeoffDistance50ftBarrier: (weightTakeoff, tempCTakeoff, pressAltTakeoff, headwindTakeoff) => {
        let x;

        if (headwindTakeoff >= 0) { x = 9 } else { x = 2 }

        const takeoffDistance = ((val.C27) + val.C28 *  weightTakeoff + val.C29 *  tempCTakeoff + val.C30 *
            pressAltTakeoff + ( weightTakeoff - val.C12) * (( weightTakeoff - val.C12) * val.C31) + 
            ( tempCTakeoff - val.C14) * (( weightTakeoff - val.C12) * val.C32) + ( tempCTakeoff - val.C14) *
            (( tempCTakeoff - val.C14) * val.C33) + ( pressAltTakeoff - val.C17) * 
            (( weightTakeoff - val.C12) * val.C34) + ( pressAltTakeoff - val.C17) * 
            (( tempCTakeoff - val.C14) * val.C35) + ( pressAltTakeoff - val.C17) * 
            (( pressAltTakeoff - val.C17) * val.C36)) * (1 - headwindTakeoff*0.1/x);
        
        return takeoffDistance; 
    },
    ClimbRate: (tempCTakeoff, pressAltTakeoff) => {
        const climbRate = val.C37 + -val.C38 *  tempCTakeoff + -val.C39 *  pressAltTakeoff + 
            ( tempCTakeoff - val.C40) * (( tempCTakeoff - val.C40) * val.C41) + 
            ( pressAltTakeoff - val.C42) * (( tempCTakeoff - val.C40) * val.C43) + 
            ( pressAltTakeoff - val.C42) * (( pressAltTakeoff - val.C42) * -val.C44);
        return climbRate;
    },
    LandingRoll: (tempCLanding, pressAltLanding, headwindLanding) => {
        let x;

        if (headwindLanding >= 0) { x = 9 } else { x = 2 }

        const landingDistance = ((val.C45 + val.C46 *  tempCLanding + val.C47 *  pressAltLanding + 
            ( tempCLanding - val.C14) * (( tempCLanding - val.C14) * -val.C48) + ( pressAltLanding - val.C17) * 
            (( tempCLanding - val.C14) * val.C49) + ( pressAltLanding - val.C17) * 
            (( pressAltLanding - val.C17) * val.C50)) * (1-headwindLanding*0.1/x));

        return landingDistance;
    },
    LandingDistance50ftBarrier: (tempCLanding, pressAltLanding, headwindLanding) => {
        let x;

        if (headwindLanding >= 0) { x = 9 } else { x = 2 }

        const landingDistance50ftBarrier = ((val.C51 + val.C52 *  tempCLanding + val.C53 *  pressAltLanding + 
            ( tempCLanding - val.C14) * (( tempCLanding - val.C14) * val.C48) + ( pressAltLanding - val.C17) *
            (( tempCLanding - val.C14) * 0.000135833333333333) + ( pressAltLanding - val.C17) *
            (( pressAltLanding - val.C17) * val.C55)) * (1-headwindLanding*0.1/x));

        return landingDistance50ftBarrier;
    }
};

